'use client'
import React, { useState } from 'react';
import styles from './X_o.module.scss';

const X_o = () => {
  const [board, setBoard] = useState(Array(9).fill(''));
  const [currentPlayer, setCurrentPlayer] = useState('X');
  const [winner, setWinner] = useState(null);
  const [winningCells, setWinningCells] = useState([]);

  const winningConditions = [
    [0, 1, 2], [3, 4, 5], [6, 7, 8], // rows
    [0, 3, 6], [1, 4, 7], [2, 5, 8], // columns
    [0, 4, 8], [2, 4, 6]             // diagonals
  ];

  const handleClick = (index) => {
    if (board[index] !== '' || winner) return;
    
    const newBoard = [...board];
    newBoard[index] = currentPlayer;
    setBoard(newBoard);
    
    checkWinner(newBoard);
    setCurrentPlayer(currentPlayer === 'X' ? 'O' : 'X');
  };

  const checkWinner = (currentBoard) => {
    for (let i = 0; i < winningConditions.length; i++) {
      const [a, b, c] = winningConditions[i];
      if (currentBoard[a] && currentBoard[a] === currentBoard[b] && currentBoard[a] === currentBoard[c]) {
        setWinner(currentBoard[a]);
        setWinningCells([a, b, c]);
        return;
      }
    }
    
    if (!currentBoard.includes('')) {
      setWinner('D'); // D for Draw
    }
  };

  const resetGame = () => {
    setBoard(Array(9).fill(''));
    setCurrentPlayer('X');
    setWinner(null);
    setWinningCells([]);
  };

  const renderCell = (index) => {
    const isWinningCell = winningCells.includes(index);
    return (
      <div
        key={index}
        className={`${styles.cell} ${isWinningCell ? styles.winningCell : ''}`}
        onClick={() => handleClick(index)}
      >
        {board[index] && <span>{board[index]}</span>}
      </div>
    );
  };

  return (
    <div className={styles.container}>
      <h1 className={styles.title}>RETRO TIC-TAC-TOE</h1>
      <div className={styles.status}>
        {winner 
          ? (winner === 'D' ? 'Draw!' : `Player ${winner} wins!`)
          : `Player ${currentPlayer}'s turn`
        }
      </div>
      
      <div className={styles.board}>
        {Array(9).fill().map((_, index) => renderCell(index))}
      </div>
      
      <button className={styles.resetButton} onClick={resetGame}>
        re-match
      </button>
    </div>
  );
};

export default X_o;